        url('./fonts/PPSupplyMono-Regular.woff') format('woff'),
        url('./fonts/PPSupplyMono-Regular.ttf') format('truetype'),
        url('./fonts/PPSupplyMono-Regular.svg#PPSupplyMono-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}


